<template>
  <draggable
    class="item-wrapper"
    tag="div"
    :list="list"
    :handle="handle"
    :group="group"
  >
    <div class="item-group" :key="el[dataKey]" v-for="el in list">
      <div class="item">
        <div
          v-show="hasChildren(el)"
          @click="onToggleExpand(el[dataKey])"
          class="expand-icon"
          :class="{ expanded: isExpanded(el[dataKey]) }"
        >
          <a-icon type="right"/>
        </div>
        <slot :data="el"/>
      </div>
      <nested-draggable
        v-show="!hasChildren(el) || isExpanded(el[dataKey])"
        v-bind="passProps"
        class="item-sub"
        :list="el.children"
      >
        <template #default="{ data }">
          <slot :data="data"/>
        </template>
      </nested-draggable>
    </div>
  </draggable>
</template>

<script>
import Draggable from 'vuedraggable'
import { hasChildren } from '@/libs/utils'
import _omit from 'lodash/omit'
import Emitter from '@c/mixins/Emitter'

const EXPAND_ALL_EVENT_KEY = 'nested-draggable.expand-all'
const COLLAPSE_ALL_EVENT_KEY = 'nested-draggable.collapse-all'

export default {
  name: 'NestedDraggable',
  componentName: 'NestedDraggable',
  components: {
    Draggable,
  },
  mixins: [
    Emitter,
  ],
  data() {
    return {
      expanded: [],
    }
  },
  props: {
    list: Array,
    group: {
      type: String,
      default: 'default',
    },
    handle: {
      type: String,
      default: '.item',
    },
    expandKeys: {
      type: Array,
      default: () => [],
    },
    dataKey: {
      type: String,
      default: 'id',
    },
  },
  computed: {
    passProps() {
      return _omit(this.$props, [
        'list',
      ])
    },
    listKeys() {
      return this.list.map((i) => i[this.dataKey])
    },
  },
  created() {
    this.$on(EXPAND_ALL_EVENT_KEY, this.expandAll)
    this.$on(COLLAPSE_ALL_EVENT_KEY, this.collapseAll)
  },
  beforeDestroy() {
    this.$off(EXPAND_ALL_EVENT_KEY, this.expandAll)
    this.$off(COLLAPSE_ALL_EVENT_KEY, this.collapseAll)
  },
  methods: {
    hasChildren,
    onToggleExpand(id) {
      const index = this.expanded.indexOf(id)
      if (index === -1) {
        this.expanded.push(id)
      } else {
        this.expanded.splice(index, 1)
      }
    },
    isExpanded(id) {
      return this.expanded.indexOf(id) !== -1
    },
    expandAll() {
      this.expanded = [...this.listKeys]
      this.broadcast('NestedDraggable', EXPAND_ALL_EVENT_KEY)
    },
    collapseAll() {
      this.expanded = []
      this.broadcast('NestedDraggable', COLLAPSE_ALL_EVENT_KEY)
    },
  },
  watch: {
    expandKeys: {
      handler(newVal) {
        this.expanded = newVal
      },
      immediate: true,
    },
  },
}
</script>

<style scoped lang="less">
@import "~@/styles/vars";

.item-wrapper {
  max-width: 100%;
  margin: 0;
}

.item {
  height: 50px;
  display: flex;
  padding: 0 15px 0 5px;
  align-items: center;
  border: @border-base;
  transition: background-color .25s ease;
  margin-bottom: 8px;

  &:hover {
    background-color: @blue-1;
  }
}

.item-sub {
  margin-left: 20px;
}

.sortable-ghost {
  background-color: @blue-1;
  border: 2px dashed @primary-color;
}

.expand-icon {
  cursor: pointer;
  padding: 5px;
}

.expanded {
  transform: rotate(90deg);
}
</style>
